<template>
  <div class="all">
    <div class="class">
      <span>2016 — 2020</span>
      <span>obor informační technologe</span>
    </div>
    <div class="img">
      <img :src="'./assets/' + this.index + '.gif'" alt="fotka s glitch efektem" />
    </div>
    <div class="arrows">
      <router-link :to="'/' + linkLeft" class="left">
        <div></div>
        <div></div>
        <div></div>
      </router-link>
      <router-link :to="'/' + linkRight" class="right">
        <div></div>
        <div></div>
        <div></div>
      </router-link>
    </div>
    <div class="info">
      <span class="name">
        <span class="glitch gl1" :name="details[index - 1].fName">{{ details[index - 1].fName }}</span>
        <span class="glitch gl2" :name="details[index - 1].lName">{{ details[index - 1].lName }}</span>
      </span>
      <span class="nickname">{{ details[index - 1].nName }}</span>
      <p class="about">“{{ details[index - 1].about }}”</p>
    </div>
    <router-link to="/" class="back">
      <div class="arrow-back">
        <div></div>
        <div></div>
      </div>
      <div class="arrow-back">
        <div></div>
        <div></div>
      </div>
      <span>celá třída</span>
    </router-link>
  </div>
</template>

<script>
import Details from "../details.json";
export default {
  data() {
    return {
      index: 1,
      linkLeft: 0,
      linkRight: 0,
      details: Details,
      prevImg: {},
      nextImage: {}
    };
  },
  methods: {
    getIndex() {
      this.index = Number(this.$route.params.id);
    },
    setLink() {
      if (this.index + 1 < 23) {
        this.linkRight = this.index + 1;
      } else {
        this.linkRight = 1;
      }
      if (this.index - 1 > 0) {
        this.linkLeft = this.index - 1;
      } else {
        this.linkLeft = 22;
      }
    }
  },
  mounted() {
    this.getIndex();
    this.setLink();
    let prevImgSetter = new Image();
    let nextImgSetter = new Image();
    prevImgSetter.src = "./assets/" + (this.index - 1) + ".gif";
    nextImgSetter.src = "./assets/" + (this.index + 1) + ".gif";
    this.prevImg = prevImgSetter;
    this.nextImg = nextImgSetter;
  },
  watch: {
    $route(to, from) {
      this.getIndex();
      this.setLink();
      this.prevImg.src = "./assets/" + (this.index - 1) + ".gif";
      this.nextImg.src = "./assets/" + (this.index + 1) + ".gif";
    }
  },
  computed: {}
};
</script>

<style>
.gl1::before,
.gl3::after {
  content: attr(name);
}

.gl2::before,
.gl4::after {
  content: attr(name);
}
</style>